import { Card } from 'antd';
import { images } from '../../../../assets/images';



interface ProjectsCardProps {
   title: string;
   description: string;
   cover_image_url: string;
}

function ProjectsCard(props: ProjectsCardProps) {
   return (
      <Card
         hoverable
         bordered={true}
         cover={<img style={{ height: '160px', objectFit: 'cover' }} alt="example" src={images[props.cover_image_url]} />}
      >
         <Card.Meta title={props.title} description={<div> {props.description}<br /><a href="#/projects/example_post_1">Read More</a></div>} />
      </Card>
   );
}

export default ProjectsCard;
